body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
} 

@font-face {
  font-family: "Exotc350 Bd BT";   /*Can be any text*/
  src: local("Exotc350BdBTBold"),
    url("./fonts/Exotc350BdBTBold.ttf") format("truetype");
}

/* leaderboard index page styling */

.body{
  width: auto;
  height: auto;
  font-family: 'AvertaStd-Semibold';
}

.container1{
  width: 100%;
  height: auto;
  background: url("/public/imageback.png"), #0D0D0D;
  text-align: center;
  padding: 30px 0 60px;
}

.header1{
  font-size: 40px;
  line-height: 47px;
  color: #ffffff;
}

.searchContainer{
  width: 50%;
  height: 40px;
  display: flex;
  margin: auto;
  border-radius: 15px;
  background-color: #0D0D0D;
  justify-content: center;
  align-items: center;
  
}

.searchBox{
  width: 90%;
  height: 40px;
  padding-left: 10px;
  background-color: #0D0D0D;
  color: #ffffff;
  border-radius: 10px 0 0 10px;
  border: none;
  box-shadow: 0px 0px 10px #396B6B;
}

.searchBox :focus {
  outline: none;
}

.searchBackground{
  background-color: #00E3A5;
  width: 10%;
  height: 40px;
  border-radius: 0 10px 10px 0;
  margin: auto;
}

.imgpad{
  padding-top: 8px;
}

.container2{
  width: 100%;
  height: auto;
  background-color: #0D0D0D;
}

.chartContainer{
  width: 80%;
  height: 384px;
  background-color: #0D0D0D;
  border-radius: 24px;
  margin: auto;
  box-shadow: 0px 0px 10px #396B6B;
}

.chartText{
  color: #ffffff;
  margin: auto;
  padding: 40px;
}

.headerSmall{
  font-size: 20px;
  line-height: 16px;
  text-align: center;
  background: linear-gradient(59.68deg, #00CDB8 -17.3%, #AD98FF 116.57%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  padding-top: 70px;
}

.texts{
  font-size: 20px;
  line-height: 23px;
  text-align: center;
  color: #ffffff;
  width: 60%;
  margin: auto;
  padding-bottom: 40px;
}

.tableContainer{
  width: 80%;
  height: auto;
  background-color: #0D0D0D;
  box-shadow: 0px 0px 10px #396B6B;
  margin: auto;
  border-radius: 25px;
  padding: 20px;
  font-family: 'AvertaStd-Regular';
}

.navigation{
  display: flex;
  width: 40%;
  height: 70px;
  
  margin-left: 660px;
  justify-content: space-between;
  align-items: center;
}

.flex2{
  width: 30%;
